import { Button, TextInput } from "@react-native-material/core";
import { HeaderBackButton } from "@react-navigation/elements";
import React, { useEffect, useMemo } from "react";
import { View, StyleSheet } from "react-native";
import { log } from "utils/log";
import { componentConstants } from "../components/utils/component-contants";
import HideKeyboard from "../components/utils/hide-keyboard";
import { ViewLandscape } from "../components/utils/view-landscape";
import TaskCategoryModel from "../models/category/task-category-model";
import { CategoryValidator } from "../models/category/task-category-validator";
import { createTaskCategoryService } from "../services/task-category-service";
import { layoutConstants } from "../themes/layout-contants";
import { categoryTags } from "../config/suggestions/category-tags";
import { Dropdown } from "react-native-element-dropdown";
import TaskList from "../components/task/task-list";

export default function CategoryDetailScreen({ route, navigation }) {
  // on init, title are null
  // on update, title base on data model
  // on action create/update form, category is submited
  const { params } = route;
  const category = params?.category;
  const back = params?.back;
  const [title, setTitle] = React.useState(category?.title || "");
  const isUpdate = !!category;
  const taskCategoryService = createTaskCategoryService();
  const [isFocus, setIsFocus] = React.useState(false);
  const [categoryUpdate, setCategoryUpdate] = React.useState(null);
  const suggestionCategories = useMemo(() => categoryTags.map(c => ({ value: c.name, label: c.name })));

  useEffect(() => {
    navigation.setOptions({
      headerLeft: (props) => (
        <HeaderBackButton
          {...props}
          onPress={async () => await saveAndBack()}
          label={back?.component || componentConstants.ACTIONS_SCREEN}
          labelVisible={true}
          tintColor={layoutConstants.textColor}
        />
      ),
    });
  }, []);

  const saveAndBack = async () => {
    let newCategory;
    try {
      newCategory = await handelSave(category, title, isUpdate);
    } catch (error) {
      log.warn('Can not save category: ', error)
    }
    navigation.navigate(back?.component || componentConstants.ACTIONS_SCREEN, {
      action: "reload",
      categoryId: newCategory
        ? newCategory.id
        : null,
    });
  };

  const handelSave = async (category, newTitle, isUpdate) => {
    const id = category ? category.id : null;
    let newCategory = new TaskCategoryModel({
      id,
      title: newTitle,
    });
    // validate category here
    await CategoryValidator.validate(newCategory);
    await taskCategoryService.saveCategories(newCategory, isUpdate);
    return newCategory;
  };

  const onDelete = (category) => {
    navigation.navigate("Actions", { action: "deleteCategory", item: category });
  };

  return (
    <ViewLandscape>
      <View style={{ flex: 1 }}>
        <View>
          <TextInput
            variant="standard"
            value={title}
            onChangeText={setTitle}
            style={{ color: layoutConstants.textColor, padding: 10 }}
            placeholder="Category Title"
          />
        </View>
        <Dropdown
          style={[styles.dropdown, isFocus && { borderColor: "blue" }]}
          placeholderStyle={styles.placeholderStyle}
          selectedTextStyle={styles.selectedTextStyle}
          inputSearchStyle={styles.inputSearchStyle}
          iconStyle={styles.iconStyle}
          data={suggestionCategories}
          search
          maxHeight={300}
          labelField="label"
          valueField="value"
          placeholder={!isFocus ? "Suggestion" : "..."}
          searchPlaceholder="Search..."
          value={categoryUpdate?.value}
          onFocus={() => setIsFocus(true)}
          onBlur={() => setIsFocus(false)}
          onChange={(item) => {
            setCategoryUpdate(item);
            setIsFocus(false);
          }}
        />
        <TaskList
          tasks={categoryTags[0].actions}
          onPress={() => { }}
        />
        <HideKeyboard>
          <View style={{ flex: 1 }}></View>
        </HideKeyboard>
        <View>
          {isUpdate && (
            <Button
              color="red"
              title={"Delete"}
              tintColor={"white"}
              onPress={() => onDelete(category)}
              style={{ width: "100%", marginBottom: 20 }}
            />
          )}
          <Button
            disabled={!title}
            title={isUpdate ? "Update" : "Create"}
            onPress={async () => await saveAndBack()}
            color={layoutConstants.colorPrimary}
            tintColor={"white"}
            style={{ width: "100%", marginBottom: 20 }}
          />
        </View>
      </View>
    </ViewLandscape>
  );
}
const styles = StyleSheet.create({
  container: {
    backgroundColor: "white",
    flex: 1,
  },
  dropdown: {
    height: 50,
    borderColor: "gray",
    borderBottomWidth: 1,
    paddingHorizontal: 8,
  },
  placeholderStyle: {
    fontSize: 16,
  },
  selectedTextStyle: {
    fontSize: 16,
  },
  iconStyle: {
    width: 20,
    height: 20,
  },
  inputSearchStyle: {
    height: 40,
    fontSize: 16
  },
});